import {
  Error,
  Label,
  AddImage,
  InputHidden,
  AddPhoto,
  TitleAddPhoto,
  ImgBox,
  Image,
  Comment,
  ButtonClose,
  CancelIcon,
  CommentTitle,
} from './ModalAddsPet.styled';

export const FormePageSecond = ({ setImgUrl, imgUrl }) => {
  return (
    <>
      <TitleAddPhoto> Add photo and some comments</TitleAddPhoto>
      <Label>
        <AddImage className={!imgUrl ? 'Add_Image_show' : ''}>
          {!imgUrl && (
            <InputHidden
              type="file"
              name="photo"
              value={imgUrl ? imgUrl : ''}
            />
          )}
          <ImgBox className={imgUrl ? 'show_img' : ''}>
            <Image src={imgUrl} alt="" />
          </ImgBox>
          <AddPhoto />
        </AddImage>
        <Error name="photo" component="p"></Error>

        <ButtonClose
          onClick={() => {
            setImgUrl(null);
          }}
          className={imgUrl ? 'button_closse__show' : ''}
        >
          <CancelIcon />
        </ButtonClose>
      </Label>

      <Label htmlFor="">
        <CommentTitle> Comments</CommentTitle>
        <Comment
          component="textarea"
          name="comment"
          placeholder="Type comments"
        />
        <Error name="comment" component="p"></Error>
      </Label>
    </>
  );
};
